@import "partial/__autoload";

@include globals;

.carousel {
  width: 100vw;
  height: rem(300px);
  position: relative;
  background-color: darkcyan;

  > div,
  button {
    /* overflow-x: hidden;overflow-y: hidden; */
    background-color: transparent;
  }

  &-indicators {
    position: absolute;
    bottom: 0;
    left: 0;
    @include size(100%, max-content);
    @include flexCenter($v: true);
    column-gap: rem(5px);
    padding: rem(10px);

    button {
      @include size(rem(15px), rem(2.5px));
      background: lighten(gray, 10%);
    @include transition(all);

      &.active {
        background: $seccol;
      }
    }
  }
  &-inner {
    @include size($s: 100%);
    display: grid;
    grid-template-columns: repeat(3, 100%);
    overflow-x: hidden;

    .carousel-item {
      padding: rem(30px);
    }
  }
  &-control-prev {
    left: 0;
  }
  &-control-next {
    right: 0;
  }
  &-control-prev,
  &-control-next {
    position: absolute;
    top: 0;
    @include size(max-content, 100%);
    color: $seccol;
    font-weight: 900;
    font-size: rem(fontsize(xl));
    padding: 0 rem(10px);
  }
}
